import "app/lib/jquery-ui";
import template from "templates/image";

const HEIGHT_MARGIN = 0;

export default Backbone.View.extend({
    className: "image-draggable",

    events: {
        "click .remove": "removeImage"
    },

    render: function() {
        this.$el.html(template(this.model.toJSON()));
        this.setup();

        return this;
    },

    setup: function() {
        let {clientWidth, clientHeight} = $(".images-container")[0];
        let $img = this.$("img");
        var self = this;
        var fromFile = false;
        if(self.model.attributes.size.width) fromFile = true;

        $img.on("load", (e) => {
            let {width, height} = e.target;
            let owidth = width, oheight = height;
            // if img is loaded from file
            if(fromFile) {
                owidth = self.model.attributes.originalSize.width;
                width = self.model.attributes.size.width;
                oheight = self.model.attributes.originalSize.height;
                height = self.model.attributes.size.height;
            }
            let aspect = width / height;

            // Save original w/h
            this.model.set({
                originalSize: {width: owidth, height: oheight},
                imageEl: e.target,
                visible: true
            });

            if (height + HEIGHT_MARGIN > clientHeight && !fromFile) {
                height = clientHeight - HEIGHT_MARGIN;
                width = height * aspect;
            }

            let left = (clientWidth - width) / 2;
            let top = (clientHeight - height) / 2;
            // if img is loaded from file
            if(fromFile) {
                left = self.model.attributes.position.left;
                top = self.model.attributes.position.top;
            }
            // Set size and position
            this.$el.width(width);
            this.$el.height(height);
            this.$el.css({
                left: `${left}px`,
                top: `${top}px`
            });

            // Set model
            this.model.set({
                position: {left, top},
                size: {width, height}
            });

            // Add classes and events
            this.$el.addClass("active");
            this.$el.draggable({
                cancel: ".remove",
                containment: "parent",
                drag: (e, ui) => {
                    this.model.set({
                        position: ui.position
                    })
                }
            });
            this.$el.resizable({
                aspectRatio: aspect,
                maxWidth: clientWidth,
                maxHeight: clientHeight,
                handles: "se, nw",
                resize: (e, ui) => {
                    this.model.set({
                        size: ui.size,
                        position: ui.position
                    })
                }
            });
        });
    },

    removeImage: function() {
        this.model.destroy();
        this.remove();
    }
});



