import "styles/style.scss";
import "babel-polyfill";
import {Enum} from "enumify";
import LayoutView from "app/views/layout";

// Enums
class Mode extends Enum {}
Mode.initEnum(["FREE", "FORMATION"]);
global.Mode = Mode;

// Events
global.Vent = {};
_.extend(Vent, Backbone.Events);

// Init the app
window.forSave = {
    images: []
};
new LayoutView();



